'use client';  
import React, { useState } from 'react';  
import {   
  Calendar, Clock, Users, Home, BookOpen,   
  DoorOpen, Menu, X, School, DoorClosed,   
  ChevronsLeft, ChevronsRight   
} from 'lucide-react';  

// Dynamically import all page components  
const componentMap = {  
  dashboard: React.lazy(() => import('@/Components/DashboardPage/dashboard')),  
  timetable: React.lazy(() => import('@/Components/TimetablePage/timetable')),  
  rooms: React.lazy(() => import('@/Components/RoomsPage/rooms')),  
  lecturers: React.lazy(() => import('@/Components/LecturersPage/lecturers')),  
  classes: React.lazy(() => import('@/Components/ClassesPage/classes')),  
  courses: React.lazy(() => import('@/Components/CoursePage/courses')),  
  faculty: React.lazy(() => import('@/Components/FacultyPage/faculty')),  
  program: React.lazy(() => import('@/Components/ProgramPage/program')),  
  departments: React.lazy(() => import('@/Components/DepartmentPage/departments')),  
  timeslots: React.lazy(() => import('@/Components/TimeslotPage/timeslot'))  
};  

const TimetableManagementDashboard = () => {  
  const [activePage, setActivePage] = useState('dashboard');  
  const [menuOpen, setMenuOpen] = useState(false);  
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);  

  // Error boundary fallback  
  const ErrorFallback = () => (  
    <div className="p-4 bg-red-100 text-red-800">  
      An error occurred while loading the page.  
    </div>  
  );  

  // Render page content with error handling  
  const renderPageContent = () => {  
    const PageComponent = componentMap[activePage];  
    
    return (  
      <React.Suspense fallback={<div>Loading...</div>}>  
        <ErrorBoundary fallback={<ErrorFallback />}>  
          <PageComponent />  
        </ErrorBoundary>  
      </React.Suspense>  
    );  
  };  

  // Navigation items with consistent structure  
  const navigationItems = [  
    { page: 'dashboard', title: 'Dashboard', icon: Home },  
    { page: 'timetable', title: 'Timetable', icon: Calendar },  
    { page: 'faculty', title: 'Faculty', icon: School },  
    { page: 'departments', title: 'Departments', icon: BookOpen },  
    { page: 'program', title: 'Program', icon: DoorOpen },  
    { page: 'lecturers', title: 'Lecturers', icon: Users },  
    { page: 'courses', title: 'Courses', icon: Clock },  
    { page: 'classes', title: 'Classes', icon: Users },  
    { page: 'rooms', title: 'Rooms', icon: DoorClosed },  
    { page: 'timeslots', title: 'Timeslots', icon: Clock }  
  ];  

  // Error Boundary Component  
  class ErrorBoundary extends React.Component {  
    constructor(props) {  
      super(props);  
      this.state = { hasError: false };  
    }  

    static getDerivedStateFromError(error) {  
      return { hasError: true };  
    }  

    componentDidCatch(error, errorInfo) {  
      console.error("Caught an error:", error, errorInfo);  
    }  

    render() {  
      if (this.state.hasError) {  
        return this.props.fallback;  
      }  

      return this.props.children;  
    }  
  }  

  return (  
    <div className="flex min-h-screen bg-gray-100">  
      {/* Fixed Header */}  
      <div className="fixed top-0 left-0 w-full bg-blue-900 text-white text-center text-2xl font-bold py-4 z-50">  
        Timetable Management System  
      </div>  

      {/* Main Content Area */}  
      <main className="flex-1 mt-16 p-6 w-full">  
        {renderPageContent()}  
      </main>  
    </div>  
  );  
};  

export default TimetableManagementDashboard;  